.Burger{
    width:100%;
    margin:auto;
    height: 250px;
    overflow:scroll;
    text-align: center;
    font-weight:bold;
    font-size: 1.2rem;
}
.Burger::-webkit-scrollbar {
    display: none;
  }

@media (min-width:10500px00px) and (min-height:400px){
    .Burger{
        width:350px;
        height:400px;
    }
}
@media (min-width:500px) and (min-height:401px){
    .Burger{
        width:450px;
        height:400px;
    }
}

@media (min-width:1000px) and (min-height:700px){
    .Burger{
        width:700px;
        height:600px;
    }
}